import React, { Component } from 'react';

class AudioAnalyser extends Component {
      constructor(props) {
            super(props);
            this.state = { audioData: new Uint8Array(0) };
            this.tick = this.tick.bind(this);
            this.canvas = React.createRef();
      }
      componentDidMount() {
            this.audioContext = new (window.AudioContext || window.webkitAudioContext)();
            this.analyser = this.audioContext.createAnalyser();
            this.dataArray = new Uint8Array(this.analyser.frequencyBinCount);
            this.source = this.audioContext.createMediaStreamSource(window.localStream);
            this.source.connect(this.analyser);
            this.rafId = requestAnimationFrame(this.tick);
      }
      componentWillUnmount() {
            cancelAnimationFrame(this.rafId);
            this.analyser.disconnect();
            this.source.disconnect();
      }
      tick() {
            this.analyser.getByteTimeDomainData(this.dataArray);
            this.setState({ audioData: this.dataArray });
            this.rafId = requestAnimationFrame(this.tick);
      }
      draw() {
            const audioData = this.state.audioData;
            const canvas = this.canvas.current;
            const height = canvas.height;
            const width = canvas.width;
            const context = canvas.getContext('2d');
            if (this.props.type === 'sine') {
                  let x = 0;
                  const sliceWidth = (width * 1.0) / audioData.length;
                  context.lineWidth = 2;
                  context.strokeStyle = '#000000';
                  context.clearRect(0, 0, width, height);
                  context.beginPath();
                  context.moveTo(0, height / 2);
                  for (const item of audioData) {
                        const y = (item / 255.0) * height;
                        context.lineTo(x*2, y);
                        x += sliceWidth;
                  }
                  context.lineTo(x, height / 2);
                  context.stroke();
            } else if (this.props.type === 'bar') {
                  var bufferLength = this.analyser.frequencyBinCount;
                  var barWidth = (width / (bufferLength/4 )) * 10;
                  var barHeight;
                  var x = 0;
                  context.clearRect(0, 0, width, height);
                  context.beginPath();
                  context.moveTo(0, height / 2);
                  for (var i = 0; i < (bufferLength/4); i++) {
                        barHeight = audioData[i] / 2;

                        context.fillStyle = 'rgb(' + (barHeight + 100) + ',50,50)';
                        context.fillRect(x, height - barHeight / 2, barWidth, barHeight);

                        x += barWidth + 1;
                  }
            }
      }
      componentDidUpdate() {
            this.draw();
      }
      render() {
            return <canvas ref={this.canvas} className={this.props.className} />;
      }
}

export default AudioAnalyser;