import { BrowserRouter as Router, Switch, Route, Redirect, Link } from 'react-router-dom';

// import Home from './views/home';
import Video from './views/video';
// import Projects from './views/projects';

const isAuth = true;

function App() {

      return (
            <Router>
                  <Switch>
                        <Route exact path="/">
                              <div className="text-4xl text-center"><Link to="/room/abc-defg-hij">GO</Link></div>
                        </Route>
                        {/* <Route exact path="/about" component={ About } /> */}
                        {/* <Route exact path="/projects" component={ Projects } /> */}
                        <ProtectedRoutes exact path="/p">Protection</ProtectedRoutes>
                        <Route exact path={[ "/room/:url" ]} component={ Video } />
                        <Redirect to="/"/>
                  </Switch>
            </Router>
      );
}

function ProtectedRoutes({children, ...props}){
      return(
            <Route {...props} render={({location})=>{
                  return isAuth ? (
                        children
                  ) : (
                        <Redirect to={{ pathname: '/', state: { from: location } }}/>
                  )
            }}>

            </Route>
      );
}

export default App;
